<template>
  <div class="login">
         <el-card class="login-card">
              <el-form ref="form" class="form"  :model="sizeForm" label-position="left" label-width="60px" size="medium ">
                       <el-form-item label="账号" class="login-item">
                           <el-input   prefix-icon="el-icon-user-solid"   v-model.trim="sizeForm.name"  :clearable='true'  placeholder="请输入账号"></el-input>
                      </el-form-item>
                       <el-form-item label="密码" class="login-item">
                          <el-input prefix-icon="el-icon-key" type='password' v-model.trim="sizeForm.password"  :clearable='true'  placeholder="请输入密码"></el-input>
                      </el-form-item>
                      <el-form-item label="验证码" class="login-item">
                         <el-input  prefix-icon="el-icon-s-comment" v-model.trim="sizeForm.yzcode" style="width:73%"  placeholder="请输入验证码"></el-input>
                        <a title="点我,刷新验证码">
                          <svg v-html="yzms" @click="getyzm" style="margin: -15px 0px ;cursor: pointer;display: inline-block;width:80px;height:40px;"></svg>
                        </a>
                      </el-form-item>
                    <el-form-item size="large" class="login-item button-item">
                        <el-button type="primary" @click="onSubmit" :disabled="!sizeForm.name ||!sizeForm.password || !sizeForm.yzcode">立即登录</el-button>
                        <el-button @click="resetForm" :disabled="!sizeForm.name && !sizeForm.password && !sizeForm.yzcode">重置</el-button>
                    </el-form-item>
              </el-form>
        </el-card>
  
  </div>
</template>

<script>
import md5 from 'js-md5';
export default {
    data() {
      return { 
        yzms:"",
         sizeForm: {
          name: '',
          password: '',
          yzcode:'',
        },
          uers:[]
      };
    },
  mounted(){   

  },
   created(){
   this.getinfo();
   },
    methods:{
      getinfo(){
         this.$axios.post('/API/getinfo',{
         }).then(res => { 
           if(res.data.code=='1'){
            this.$confirm('尊敬的'+res.data.data.name+'用户您好!,系统检测到您之前登录过此网站,现在您可免登陆进入,是否继续?', '提示', {
                confirmButtonText: '是,免登陆进入',
                cancelButtonText: '否,我需要重新登录',
                type: 'warning'
              }).then(() => {
                  this.$message.success('免登录成功');
                  this.$router.replace({path:'/home'})
              }).catch(() => {
                  this.getyzm()
              });
           }else{
             this.getyzm();
           }
         }).catch(err => {
             this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
         })
      },

    getyzm(){
        this.$axios.post('/API/yzm',{
         }).then(res => { 
           this.yzms=res.data
         }).catch(err => {
             this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
         })
    },
    onSubmit(){ 
      let param = new URLSearchParams()
      param.append('userid', this.sizeForm.name)
      param.append('password', md5(this.sizeForm.password))
      param.append('yzcode', this.sizeForm.yzcode)
         this.$axios.post('/API/login',param).then(res => {
             if(res.data.code=='1'){
                 this.$message.success('登录成功');
                 this.$router.replace({path:'/home'});
             }else{
                this.sizeForm.yzcode='';
                this.$message.warning(res.data.message);
                this.getyzm();
             }
         }).catch(err => {
              this.$alert('服务器异常,请联系管理员', '提示', {
              confirmButtonText: '确定',
              type: 'error',
            });
         })
        
      },
       resetForm() {
        this.sizeForm={
          name: '',
          password: '',
          yzcode:'',
        }
      },
  

    }
  }
</script>
<style >
.login{
  height: 100vh;
  width: 100vw;
  overflow: hidden; 
  background: url("../assets/login-bg.jpg") no-repeat;
  background-size: cover;
  position: relative;
  color: black;
}
  .login-card {
   /* margin: 13% auto; */
   position: absolute;
   top: 30vh;
   left: 37%;
   right: 0;
   width: 400px;
   height: 300px;
   background: transparent;
   border: none;


  }

 .el-button{
   width: 130px;
  }
.button-item{
width: 100%;
margin: 0 auto;

}
.login-item{
  margin: 25px 0;

}
 /* .login .el-input__inner {
   background: transparent !important;
  } */
/*  .login .el-card {
 color: black !important;
} */
.login .el-form-item__label {
color: black !important;
}
</style>

